
body{
    padding: 0;
    margin: 0;
    font-family: Helvetica, sans-serif;
    background-color: #f5f5f5;
    z-index: 0;
    font-size:16px;
}
h1 {
  font-size: 24px;
}
.nav-link {
  background-color: #1F2229;
  width: 100%;
  text-align: left;
  margin-bottom: 2px;
  padding: 10px 0px 10px 20px;
}
a {
  color: #9f9f9f;
  text-decoration: none;
}
a:hover, a:active{
  color: white;
}
section, header, footer {
  padding: 0 0px;
}
form {
  position: fixed;
  top: 12%;
  left: 22%;
  border: 1px solid #c6c7cc;
  border-radius: 5px;
  font: 14px/1.4 "Helvetica Neue", Helvetica, Arial, sans-serif;
  overflow: hidden;
  width: 40%;
}
header {
  width: 100%;
  height: 5%;
  top: 0;
  left: 0;
  z-index: 80;
  background-color: #09090b;
  -webkit-box-shadow: 2px 2px 5px 0 rgba(0, 0, 0, 0.095);
          box-shadow: 2px 2px 5px 0 rgba(0, 0, 0, 0.095);
  padding: 8px 16px;
  overflow: hidden;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
.menu-btn {
  float: right;
}
aside {
  background-color: #09090b;
  width: 20%;
  position: absolute;
  top: 42px;
  left: 0;
  bottom: 0px;
  z-index: 1;
  height: 100%;

  /* align-items: center; */
  -webkit-box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16), 0 3px 6px rgba(0, 0, 0, 0.23);
          box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16), 0 3px 6px rgba(0, 0, 0, 0.23);
}
aside h2 {
  font-weight: 300;
  color: #afafaf;
}
button {
  background: transparent;
  border: 0;
  font-size: 30px;
  outline: none;
  color: #9f9f9f;
  cursor: pointer;
  -webkit-transition: all .3s ease-in-out;
  transition: all .3s ease-in-out;
}
button:hover {
  -webkit-box-shadow: 0px 0px 20px rgba(52, 48, 70, 0.5);
          box-shadow: 0px 0px 20px rgba(52, 48, 70, 0.5);
  background-color: #2A2C38;
  color: white;
}
.slide-transition {
  -webkit-transition: all .3s cubic-bezier(.65, .05, .36, 1);
  transition: all .3s cubic-bezier(.65, .05, .36, 1);
}
.message-info {
  color: #777777;
  padding: 5px 0;
}
.account-action .btn {
  width: 100px;
  font-size: 16px;
  text-align: center;
  background: -webkit-gradient(linear, left top, left bottom, from(#007bdc), to(#006bbd));
  background: linear-gradient(#007bdc, #006bbd);
  border-radius: 25px;
  color: #fff;
  cursor: pointer;
  padding: 8px 16px;
  outline: none;
}
@media (min-width: 320px){
section, header, footer {
    padding: 0 calc(50vw - 160px);
}
}
@media (min-width: 480px){
section, header, footer {
    padding: 0 calc(50vw - 240px);
}
}
@media (min-width: 768px) {
section, header, footer {
    padding: 0 calc(50vw - 380px);
}
}
@media (min-width: 1000px){
section, header, footer {
    padding: 0 calc(50vw - 500px);
}
}
@media (max-width: 1500px){
form {
    width: 60%;
}
}
@media (max-width: 1000px){
form {
    position: relative;
    left: 2%;
    border-radius: 5px;
    overflow: hidden;
    width: 95%;
}
}
@media (max-width: 768px){
form {
    position: relative;
    left: 2%;
    border-radius: 5px;
    overflow: hidden;
    width: 95%;
}
}
@media (max-width: 320px){
form {
    position: relative;
    left: 2%;
    border-radius: 5px;
    overflow: hidden;
    width: 95%;
}
}
@media (max-width: 1000px){
header {
    margin-bottom: 40px;
}
}
@media (max-width: 1000px){
aside {
    display: contents;
}
}
@media (max-width: 1000px){
.PersonalButtonPosition {
    display: contents;
    width: 100%
}
}
@media (max-width: 768px){
.PersonalButtonPosition {
    display: contents;
    width: 100%
}
}
@media (max-width: 768px){
.flex-row {
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
        -ms-flex-direction: column;
            flex-direction: column;
}
}
@media (max-width: 1000px){
#buttons .btn-personalArea{
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
    list-style: none;
    text-align: center;
    background-color: #000000;
    margin: 2% 2%;
    width: 95%;
    line-height: 140px;
    border-radius: 10px;
}
}
@media (max-width: 1000px){
ul{
  margin: 0px;
  padding: 0px
}
}
@media (max-width: 768px){
#buttons .btn-personalArea{
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
    list-style: none;
    text-align: center;
    background-color: #000000;
    margin-right: 2%;
    margin-bottom: 2%;
    width: 95%;
    line-height: 140px;
    border-radius: 10px;
}
}

.PersonalArea{
  font-family: "Times New Roman", Georgia, Serif;
  font-size: 32px;
  text-align: center;
}
.PersonalButtonPosition{
  width: 60%;
  margin: 0 auto;
}
.flex-row {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
}
#buttons li {
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  list-style: none;
  text-align: center;
  background-color: #000000;
  margin-right: 2%;
  margin-bottom: 2%;
  width: 30%;
  line-height: 250px;
  border-radius: 10px;
  font-size: 16px;
}
#buttons li a {
  text-decoration: none;
  color: #FFFFFF;
  display: block;
}
#buttons li a:hover {
  text-decoration: none;
  color: #ffffff;
  -webkit-box-shadow: 0px 0px 20px rgba(52, 48, 70, 0.5);
          box-shadow: 0px 0px 20px rgba(52, 48, 70, 0.5);
  background-color: #2A2C38;
  border-radius: 10px;
}

.checked-img{
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
}
.subtitle-info-text{
margin: auto 10px;
}

table {
width: 60%;
font-size: 20px;
margin: 2% auto;
border: 1px solid #00458a;
border-collapse: separate;
empty-cells: hide;
}
th, td {
  border: 2px solid #00458a;
}

fieldset {
  border: 0;
  margin: 0;
  padding: 0;
}
input {
  font: 14px/1.4 "Helvetica Neue", Helvetica, Arial, sans-serif;
  background: #fff;
  border: 1px solid #c6c7cc;
  border-radius: 3px;
  -webkit-box-shadow: inset 0 1px 1px rgba(0, 0, 0, .1);
          box-shadow: inset 0 1px 1px rgba(0, 0, 0, .1);
  color: #636466;
  padding: 6px;
  margin-top: 6px;
  width: 15%;
}
.author {
  width: 97%;
}
.card {
  position: relative;
  background-color: white;
  border: 1px solid #c6c7cc;
  width: 400px;
  height: 200px;
  border-radius: 10px;
  margin-bottom: 40px;
  z-index: 1;
}
.card-content {
  padding: 5%
}
.card-position {
  position: relative;
}
.prev {
  position: absolute;
  top: 20px;
  left: 180px;
  background-color: white;
  border: 1px solid #c6c7cc;
  width: 400px;
  height: 200px;
  border-radius: 10px;
  margin-bottom: 40px;
  z-index: 0;
}
.date {
  background-color: #ffffff;
  outline: none;
  height: 30px;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center
}
.grey-line {
  margin-top: 30px;
  margin-bottom: 10px;
  width: 100%;
  height: 40px;
  background-color: #E4E9ED
}
.display-flex {
  margin-right: 10px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -webkit-box-align: end;
      -ms-flex-align: end;
          align-items: flex-end
}
.account-info {
  padding: 20px 20px 0 20px;
}
.account-info label {
  color: #395870;
  display: block;
  font-weight: bold;
  margin-bottom: 20px;
}
@media screen and (max-width: 768px) {
.prev {
    position: inherit;
    top: 0px;
    left: 0;
}
}
.account-action {
  background: #f0f0f2;
  border-top: 1px solid #c6c7cc;
  padding: 20px;
}
.account-action label {
  color: #7c7c80;
  font-size: 12px;
  float: left;
  margin: 10px 0 0 20px;
}
